.tu-textarea {
  position: relative;
  display: inline-flex;
  vertical-align: bottom;
  width: 100%;
  font-family: inherit; }
  .tu-textarea.is-disabled {
    cursor: not-allowed; }
    .tu-textarea.is-disabled .tu-textarea__inner {
      cursor: not-allowed; }
      .tu-textarea.is-disabled .tu-textarea__inner::-moz-placeholder {
        color: #c8d0e7; }
      .tu-textarea.is-disabled .tu-textarea__inner:-ms-input-placeholder {
        color: #c8d0e7; }
      .tu-textarea.is-disabled .tu-textarea__inner::placeholder {
        color: #c8d0e7; }
    .tu-textarea.is-disabled::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      cursor: not-allowed; }
  .tu-textarea__inner {
    display: block;
    resize: vertical;
    background-color: transparent;
    background-image: none;
    border-radius: 4px;
    border: none;
    padding: 5px 15px;
    height: 32px;
    line-height: 1.5;
    box-sizing: border-box;
    width: 100%;
    font-size: 14px;
    color: #8394be;
    box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff;
    outline: none;
    font-weight: 500;
    font-family: "";
    scroll-behavior: smooth;
    cursor: unset;
    scrollbar-width: thin; }
    .tu-textarea__inner::-webkit-scrollbar-track-piece {
      background-color: transparent;
      border-radius: 4px;
      -webkit-border-radius: 4px;
      -moz-border-radius: 4px; }
    .tu-textarea__inner::-webkit-scrollbar-corner {
      background-color: transparent; }
    .tu-textarea__inner::-webkit-scrollbar {
      width: 8px;
      height: 8px; }
    .tu-textarea__inner::-webkit-scrollbar-thumb {
      background-color: #c8d0e7;
      background-clip: padding-box;
      -webkit-border-radius: 4px;
      -moz-border-radius: 4px;
      border-radius: 4px;
      transition: 0.3s ease;
      z-index: 100; }
      .tu-textarea__inner::-webkit-scrollbar-thumb:hover {
        background-color: #bec8e4;
        cursor: pointer; }
    .tu-textarea__inner::-moz-placeholder {
      color: #bec8e4; }
    .tu-textarea__inner:-ms-input-placeholder {
      color: #bec8e4; }
    .tu-textarea__inner::placeholder {
      color: #bec8e4; }
    .tu-textarea__inner::-ms-reveal {
      display: none; }
    .tu-textarea__inner:focus {
      outline: none;
      box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-textarea__count {
    position: absolute;
    font-size: 10px;
    color: #bec8e4;
    bottom: 7px;
    right: 6px; }
